<template>
  <div>
    <h3>登录</h3>
    <div>
      账号：
      <input type="text" v-model="user.name" />
    </div>
    <div>
      密码：
      <input type="text" v-model="user.pass" />
    </div>
    <div>
      <button @click="login">登录</button>
    </div>
  </div>
</template>
<script>
import { requestLogin } from "../utils/request"
export default {
  components: {},
  data() {
    return {
      user: {
        name: "",
        pass: ""
      }
    };
  },
  methods: {
    login() {
      requestLogin(this.user).then(res=>{
        if(res.data.isok){
          this.$router.push("/index")
        }else{
          alert(res.data.info)
        }
      })
    }
  }
};
</script>
<style scoped>
</style>


