import axios from "axios";
import qs from "qs"

// const baseUrl = "/api"//开发
const baseUrl="";


//登录的接口
export const requestLogin = (data) => {
    return axios({
        url: baseUrl + "/login",
        method: "post",
        data: qs.stringify(data)
    })
}

//获取设备的接口
export const requestIndex = () => {
    return axios({
        url: baseUrl + "/findDevice",
        method: "get",
    })
}


